<template>
  <b-modal centered v-model="show" hide-footer size="custom" class="modal-sm">
    <template v-slot:modal-header>
      <h1>Войти в систему</h1>
    </template>
    <form class="form" @submit.prevent="authUser">
      <b-form-input
        type="text"
        required
        class="login"
        :state="!$v.login.$invalid"
        v-model.trim="$v.login.$model"
        placeholder="Адрес эл. почты или номер телефона"
      />

      <b-form-input
        type="password"
        required
        class="password"
        :state="!$v.password.$invalid"
        v-model.trim="$v.password.$model"
        placeholder="Пароль"
      />

      <b-button type="submit" class="submit">Войти</b-button>

      <router-link to="/registration" class="registration"
        >Зарегистрироваться</router-link
      >

      <router-link to="/forgotPassword">Забыли пароль?</router-link>
    </form>
  </b-modal>
</template>

<script>
import { required, minLength } from "vuelidate/lib/validators";

export default {
  name: "Authorization",
  data: () => ({
    login: null,
    password: null,
    show: true
  }),
  methods: {
    authUser() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        // some
        return 0;
      }
      this.$store.dispatch("loginUser", {
        login: this.login,
        password: this.password
      });
      return 0;
    }
  },
  validations: {
    login: {
      minLength: minLength(4),
      required
    },
    password: {
      minLength: minLength(6),
      required
    }
  }
};
</script>

